import React from "react";
import "./Post.sass";
import { IPost, IAuthor } from '../../interfaces';
import { PostHeader } from './PostHeader';
import { ReactHTMLContent } from '../Content/Content';
import { INestedCategory, getCategory } from '../../tool';

export interface IPostProps
{
  post: IPost,
  categories: INestedCategory[];
  author: IAuthor;
}

export class Post extends React.Component<IPostProps> {
  constructor(props) {
    super(props);
  }

  render() {
    let categories = this.props.post.frontmatter.categories.map(c => getCategory(c, this.props.categories));
    return (
      <div>
        <div>
          <PostHeader frontmatter={this.props.post.frontmatter} commentCnt={this.props.post.fields.commentCnt} author={this.props.author} categories={categories}/>
        </div>
        <div className="post-content">
          <ReactHTMLContent html={this.props.post.htmlAst}></ReactHTMLContent>
        </div>
      </div>
    );
  }
};